/* Timeline Section */
.work-experience-section {
    max-width: 100%;
    height: 400px; /* Increased height for better spacing */
    padding: 40px; /* Increased padding for a larger container */
    background: #f9f9f9;
    overflow: hidden;
    position: relative;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  }
  
  /* Section Title */
  .section-title {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: #007aff;
    margin-bottom: 20px;
  }
  
  /* Horizontal Timeline */
  .horizontal-timeline {
    display: flex;
    flex-direction: row-reverse; /* Most recent experience on the right */
    gap: 20px;
    overflow-x: auto;
    scroll-behavior: smooth;
    padding: 20px 0;
    position: relative;
  }
  
  /* Timeline Item */
  .timeline-item {
    flex: 0 0 350px; /* Larger item width */
    position: relative;
    background: #fff;
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, background-color 0.3s ease;
  }
  
  /* Hover Effect */
  .timeline-item:hover {
    transform: scale(1.05);
    background-color: #007aff;
    color: #fff;
  }
  
  /* Marker */
  .timeline-marker {
    width: 12px;
    height: 12px;
    background: #007aff;
    border-radius: 50%;
    margin-bottom: 10px;
  }
  
  /* Content Styles */
  .experience-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .experience-subtitle {
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
  }
  
  .experience-description {
    font-size: 14px;
    color: #333;
  }
  
  .timeline-item:hover .experience-subtitle,
  .timeline-item:hover .experience-description {
    color: #d9ecff;
  }
  